html, body {
  height: 100vh;
  width: 100vw;
}

.speed {
  min-width: 50px;
  min-height: 50px;
  /* margin: 50px; */
  font-family: "Roboto";
  font-size: 50px;
  border-bottom: 10px solid aqua;
}

.feedback{
  display: flex;
  flex: 1;
  flex-direction: column-reverse;
  color: white;
  background-color: #2d3436;
  background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
  background-color: #166d3b;
  background-image: linear-gradient(147deg, #166d3b 0%, #000000 74%);
}
.errors{ 
  position: absolute;
  right: 50px;
  font-size: 30px;
  margin-top: 10px;
}
.timer-box {
  margin-top: 40px;
  position: sticky;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.timer {
  position: absolute;
  height: 40px;
  width: 80px;
  padding: 10px;
  border-radius: 20px;
  background-color: red;
  color: white;
}
.text {
  margin-left: 20px;
  margin-bottom: 50px;
  letter-spacing: 3px;
  overflow: scroll;
  /* margin: 50px; */
  display: flex;

  height: 100vh;
  /* width: 100vw; */
  /* margin: 500px; */
  font-size: 25px;
   white-space: pre-wrap;
   text-align: left;

}
.parameter {
  color: pink;
}
.highlight {
  font-size: 30px;
  background-color:rgb(40, 50, 180);
  opacity: 1;
  border: rgb(40, 50, 180) solid 5px;
}
.error {
  font-size: 30px;
  background-color: red;
  opacity: 1;
  border:red solid 5px;
}

.App {
  display: flex;    
  flex-direction: column;
  background-color: #2d3436;
  background-image: linear-gradient(315deg, #2d3436 0%, #000000 74%);
  text-align: center;
  height: 100vh;
  width: 100vw;
  color: white;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.punctuation {
  color: green;
}

.operator {
  color: purple;
}
.keyword {
  color: red;
}

.function {
  color: orange;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
